.projects-box {
    color: white;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
}

.project-menu{
    width: 400px;
}
.menu-button{
    color: white;
    font-size: 12pt;

    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;

    box-sizing: border-box;
    width: 100%;
    height: 60px;
    margin: 2px 0;
    border: none;
    border-radius: 3px;
    outline: none;
}
.menu-button:hover {
    background: #ffffff3a;
    box-shadow: 0 0 8px #ffffff4d;
}

.menu-button .arrow-right{
    transition: 0.3s ease;
}
.inactive{
    background-color: #ffffff1f;
}
.active{
    transition:none;
    font-weight: bold;
    background:none;
    background-color: #ffffff69;
    box-shadow: 0 0 8px #ffffff4d;
}

.project-menu button:hover .arrow-right{
    font-weight: bold;
    margin-left: 20px;
}

.project-menu button.active .arrow-right{
    margin-left: 20px;
    transform: rotateZ(90deg);
}

.project-display{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: auto;
    box-sizing: border-box;
    width: 90%;
    height: 0px;
    padding: 0;
    overflow: hidden;
    opacity: 1;
    transition: .6s ease;
}   
.project-display .collapsed{
    opacity: 0;
}



.project-display .link-item{
    text-decoration: none;
    color: white;
    font-size: 12pt;
    padding: 10px 20px;

    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 100%;
    height: 30px;
    box-sizing: border-box;
    border: none;
    outline: none;
    margin: 2px 0;
    transition: .3s ease;
}

.project-display .link-item:hover{
    background-color: #ffffff69;
}

.project-display .link-item:first-of-type{
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.project-display .link-item:last-of-type{
    border-bottom-left-radius: 6px;
    border-bottom-right-radius: 6px;
}