.intro{
    margin: 100px auto;
    min-height: 100vh;
    display: grid;
    place-items: center;
}

.intro .text_container{
    margin: 20px 0;
    padding: 60px 40px;
    margin: 50px auto;
    background: #ffffff1f;
    font-size: 12pt;
    color: white;
    max-width: 800px;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif;
    box-sizing: border-box;
}

.intro .text_container h1{
    font-size: 1.4em;
    margin-top: 60px;
    margin-bottom: 40px;
}
.intro .text_container h1:first-of-type{
    margin-top: 0px;
}

.intro .text_container h2{
    font-size: 1.2em;
}

.intro .text_container p{
    font-size: 1.2em;
    line-height: 1.5em;
}