header{
    display: flex;

    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    flex-direction: column;

    height: 100vh;
    width: 100%;
    box-sizing: border-box;
}
header > *{
    color: white;
}

.namespace{
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 30pt;
}

.namespace h1{
    text-align: left;
    font-size: 1em;
}
.namespace p{
    font-size: .7em;
    margin: 5px;
    margin-right: 40px;
    text-align: right;
    width: 100%;
}

.namespace .windymill{
    position: absolute;
    opacity: .1;
    /* transform: rotate(20deg); */
    transform: translate(-100px, 10px);
    font-size: 15em;
}