footer{
    
    color: white;
    min-height: 100vh;
    width: 100%;
    box-sizing: border-box;

    font-family: Arial, Helvetica, sans-serif;
    -webkit-font-smoothing: antialiased;
    font-size: 1.2em;

    display: grid;
    place-items: center;
}

.f-container {
    max-width: 400px;
    text-align: center;
    box-sizing: border-box;
    width: fit-content;
}

.yritys {
    margin-bottom: 0;
    width: fit-content;
}

.contact-btn{
    background: #ffffff1f;
    height: 60px;
    width: 100%;
    border: none;
    outline: none;
    color: white;
    margin: 10px 0;
    cursor: pointer;
    font-size: 1.4em;
}
.contact-btn:hover{
    background: #ffffff36;
}

footer p {
    display: block;
    box-sizing: border-box;
    margin-bottom: 30px;
    font-size: 1em;
}
.contact-link{
    background: none;
    width: 100%;
    border: none;
    outline: none;
    margin: 10px 0;
    cursor: pointer;
    font-size: 1.4em;
}