.box-container{
    padding: 0px;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 14px;
}
.box{
    color: white;
    width: 300px;
    height: 200px;
    font-size: 1.5em;
    font-weight: bold;
    background: #ffffff1f;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
}