body {
  margin: 0;
  
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-image: linear-gradient(#03102c, black);
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  height: 100vh;
  width: 100%;
  box-sizing: border-box;
}

#page-columns {
  display: flex;
  box-sizing: border-box;
}

#header-menu{
  display: flex;
  transition: .3s ease;
  position: fixed;
  height: 100vh;
}
#header-menu.open{
  width: 260px;
}
#header-menu.closed{
  width: 60px;
}

#header-menu >* {
  transition: .3s ease;
}

.header-menu-list{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #ffffff09;
  overflow: hidden;
}
#header-menu.closed .header-menu-list{
  width: 0px;
}
#header-menu.open .header-menu-list{
  width: 200px;
}

#header-menu button{
  outline: none;
  border: none;
  color: white;
  cursor: pointer;
  background: none;
  width: 200px;
}
#header-menu button:hover{
  background: #ffffff2d;
}

.hm-list-item{
  text-decoration: none;
  text-transform: uppercase;
  font-size: 12pt;
  padding: 10px 20px;
  cursor: pointer;
  width: 100%;
  
  box-sizing: border-box;
}

#header-menu .openingBtn{
  width: 60px;
}
#menu-column{
  transition: .3s ease;
}
#menu-column.open {
  width: 260px;
}
#menu-column.closed {
  width: 0px;
}

.hamburger {
  display: flex;
  flex-direction: column;
  padding: 2px;
  transition: .3s ease;
  width: 80%;
  margin: 0 auto;
}


.hamburger .top,
.hamburger .bottom{
  width: 100%;
  height: 3px;
  border-radius: 4px;
  background-color: white;
  transition: .3s ease;
}
.hamburger.open .top,
.hamburger.open .bottom{
  opacity: 1;
}
.hamburger.closed .top,
.hamburger.closed .bottom{
  opacity: 1;
}

.hamburger .middle{
  transition: .3s ease;
  margin: 5px auto;
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hamburger.open .middle{
  width: 100%;
  height: 3px;
}
.hamburger .middle .arrowLeft{
  transition: .3s ease;
}
.hamburger.closed .middle{
  width: 100%;
  height: 2px;
}
#header-menu button:hover .hamburger .middle {
  width: 60%;
}
#header-menu button:hover .hamburger.open .middle{
  margin: 12px auto;
}
#header-menu button:hover .hamburger.open .middle .arrowLeft{
  background-color: transparent;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 0px solid transparent;
  border-right: 10px solid white;
  width: 0;
  height: 0;
  opacity: 1;
  transform: translateX(-100%);
}
#header-menu button:hover .hamburger.closed .middle .arrowLeft{
  opacity: 0;
}

#header-menu button:hover .hamburger.closed .middle{
  height: 3px;
  margin: 12px auto;
}
#header-menu button:hover .hamburger.closed .middle .arrowLeft{
  background-color: transparent;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid white;
  border-right: 0px solid transparent;
  width: 0;
  height: 0;
  opacity: 1;
  transform: translateX(100%);
}

#body-column{
  display: flex;
  flex-direction: column;
  width: 100%;
}